.translator {
  background: #f0f4f8;
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 20px auto;
  transition: all 0.3s ease;
}

.translator h1 {
  color: #333;
}

.text-area {
  width: 100%;
  height: 120px;
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 10px;
  margin-bottom: 15px;
  font-size: 16px;
  transition: border-color 0.3s;
}

.text-area:focus {
  border-color: #007bff;
  outline: none;
}

.language-selectors {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

select {
  width: 48%;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 16px;
  transition: border-color 0.3s;
}

select:focus {
  border-color: #007bff;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

button:hover:not(:disabled) {
  background-color: #0056b3;
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.translated-text {
  margin-top: 20px;
  padding: 15px;
  background: #e9ecef;
  border-radius: 5px;
}

.error {
  color: red;
  margin-top: 10px;
}

.loader {
  margin-top: 15px;
  font-size: 18px;
  color: #007bff;
  font-weight: bold;
}
